@font-face {
  font-family: "special-font";
  src: url(../../public/font/TT\ Octosquares\ Trial\ Bold.ttf);
}

#root {
  padding: 5px;
  
  max-height: 100%;
  width: 100%;

  display: flex;
}

.bg {
  transition: background var(--transition-short-ms);
}

.bg[color-theme="light"] {
  background-image: url(../../public/images/backgrounds/light-mode.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.bg[color-theme="dark"] {
  background-image: url(../../public/images/backgrounds/dark-mode.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.t-blue {
  color: rgb(40, 120, 250);
}

.s-font {
  font-family: "special-font";
  letter-spacing: 2px;
}

@media only screen and (min-width: 1100px) {
  #text-nav {
    font-size: 40px;
  }

  .text-code-scaler {
    font-size: 24px;
  }

  .text-scaler {
    font-size: 24px;
  }

  .text-header-scaler {
    font-size: 30px;
  }

  #icon-nav {
    width: 75px;
    height: 75px;
  }
}

@media only screen and (max-width: 1099px) {
  #text-nav {
    font-size: 40px;
  }

  .text-code-scaler {
    font-size: 14px;
  }

  .text-scaler {
    font-size: 16px;
  }

  .text-header-scaler {
    font-size: 24px;
  }

  #icon-nav {
    width: 75px;
    height: 75px;
  }
}

@media only screen and (max-width: 849px) {
  #text-nav {
    font-size: 22px;
  }

  .text-code-scaler {
    font-size: 13px;
  }

  .text-scaler {
    font-size: 15px;
  }

  .text-header-scaler {
    font-size: 23px;
  }

  #icon-nav {
    width: 55px;
    height: 55px;
  }
}

@media only screen and (max-width: 632px) {
  .text-code-scaler {
    font-size: 12px;
  }

  .text-scaler {
    font-size: 14px;
  }

  .text-header-scaler {
    font-size: 20px;
  }
}

@media only screen and (max-width: 470px) {
  .text-code-scaler {
    font-size: 10px;
  }
}

@media only screen and (max-width: 390px) {
  .text-code-scaler {
    font-size: 9px;
  }
}

@media only screen and (max-width: 360px) {
  .text-code-scaler {
    font-size: 8px;
  }
}

